<template>
  <div
    :class="['ccb-col',
      'ccb-col-' + span,
      responsiveClass
    ]"
    :style="style">
    <slot></slot>
  </div>
</template>

<script>
/* const prefixList = ['-moz-box-', '-webkit-box-', ''] */

import { isDef, isObject } from '@/tools/util'

export default {
  name: 'CcbFlexItem',
  props: {
    span: {
      type: Number,
      validator: v => v >= 0 && v <= 24 && Math.floor(v) === v
    },
    xxs: [Number, Object],
    xs: [Number, Object],
    sm: [Number, Object],
    md: [Number, Object],
    lg: [Number, Object],
    xl: [Number, Object],
    xxl: [Number, Object],
    order: Number
  },
  beforeMount () {
    this.bodyWidth = document.documentElement.offsetWidth
  },
  methods: {
    buildWidth (width) {
      if (typeof width === 'number') {
        if (width < 1) {
          return width
        } else {
          return width / 12
        }
      } else if (typeof width === 'string') {
        return width.replace('px', '') / this.bodyWidth
      }
    }
  },
  computed: {
    style () {
      let styles = {}
      /*
      * warp, 450%flex-item20px,  ?
      * 1. flexmargin(margin: 0 -10px), flex-itempadding(padding: 0 10px), flex
      * 2. calc, flex0 0 calc(50% - 10px)..., flex-item
      * ()
      */
      styles['padding'] = `0 ${this.$parent.gutter / 2}px`
      // flex order
      if (typeof this.order !== 'undefined') {
        styles.order = this.order
      }
      return styles
    },
    responsiveClass () {
      const classes = []
      ;['xxs', 'xs', 'sm', 'md', 'lg', 'xl', 'xxl'].forEach(size => {
        if (isDef(this[size])) {
          if (isObject(this[size])) {
            // sizeCOL props(class)
            const props = this[size]
            Object.keys[props].forEach(propName => {
              classes.push(`ccb-col-${size}${propName === 'span' ? '' : '-' + propName}-${this[size]}`)
            })
          } else {
            classes.push(`ccb-col-${size}-${this[size]}`)
          }
        }
      })
      return classes
    }
  },
  data () {
    return {
      bodyWidth: 0,
      isNewLine: false
    }
  },
  mounted () {
  }
}
</script>
